import { React, useState, useLayoutEffect, Fragment } from 'react';
import MaterialTable from 'material-table';
import RegisterCourses from './RegisterCourses';
import ModifyCourses from './ModifyCourses';
import CopyCourses from './CopyCourses';
import Popup from '../../components/UI/Popup/Popup';
import Spinner from '../../components/UI/Spinner/Spinner';
import SnackBar from '../../components/UI/SnackBar/SnackBar';
import axios from 'axios';

const styles = {
  maxWidth: '90%',
  margin: '1em auto',
};

const CoursesOverview = (props) => {
  const [snackStatus, setSnackStatus] = useState({
    openSnackBar: false,
    snackBarMessage: '',
    severity: '',
  });
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [group, setGroup] = useState([]);
  const [openRegister, setOpenRegister] = useState(false);
  const [openModify, setOpenModify] = useState(false);
  const [openCopy, setOpenCopy] = useState(false);
  const [isDeleted, setIsDeleted] = useState(false);

  useLayoutEffect(() => {
    request();
    // eslint-disable-next-line
  }, [openRegister, openModify, openCopy, isDeleted]);

  const request = () => {
    setLoading((loading) => !loading);
    //let url ='/courses-overview'
    axios
      .get('/courses-overview')
      .then((response) => {
        setData(response.data);
        setLoading((loading) => !loading);
      })
      .catch((error) => {
        setLoading((loading) => !loading);
        if (error.response) {
          //Se realizo la peticion y el servidor respondio con un codigo de status
          window.alert('Error: ' + error.response.data.message);
        } else if (error.request) {
          //Se realizo la peticion pero no se recibio una respuesta
          window.alert('Error: ' + error.request.data.message);
        } else {
          //Algo ocurrio al preparar la peticion que desencadeno un error
          console.log('Error: ' + error.message);
        }
        console.log(error.config);
      });
  };

  const deleteUserHandler = (rowData) => {
    var value = window.confirm('Eliminar ' + rowData + '?');
    if (value == true) {
      //url = /auth/delete-course/
      axios
        .delete('/auth/delete-course/' + rowData)
        .then((res) => {
          setIsDeleted((isDeleted) => !isDeleted);
          setSnackStatus({
            openSnackBar: true,
            severity: 'success',
            snackBarMessage: 'Curso eliminado correctamente',
          });
        })
        .catch((error) => {
          if (error.response) {
            //Se realizo la peticion y el servidor respondio con un codigo de status
            setSnackStatus({
              openSnackBar: true,
              severity: 'error',
              snackBarMessage: error.response.data.message,
            });
          } else if (error.request) {
            //Se realizo la peticion pero no se recibio una respuesta
            window.alert('Error: ' + error.request.data.message);
          } else {
            //Algo ocurrio al preparar la peticion que desencadeno un error
            console.log('Error: ' + error.message);
          }
          console.log(error.config);
        });
      return true;
    } else {
      return false;
    }
  };

  const downloadCoursesList = async () => {
    //Se realizan dos consultas. La primera para obtener el nombre
    //del archivo y la segunda para obtener la cedula de inscripción
    const firstUrl = '/auth/courses-list-file';
    const secondUrl = '/auth/courses-download';

    const requestOne = await axios.get(firstUrl);
    const requestTwo = await axios({
      url: secondUrl,
      method: 'GET',
      responseType: 'blob',
    });

    axios
      .all([requestOne, requestTwo])
      .then(
        axios.spread((...responses) => {
          //Se guardan las respuestas
          const fileName = responses[0];
          const fileDownload = responses[1];
          //Se realiza la descarga de la lista de cursos
          const downloadUrl = window.URL.createObjectURL(
            new Blob([fileDownload.data])
          );
          const link = document.createElement('a');
          link.href = downloadUrl;
          link.setAttribute('download', fileName.data.fileName); //any other extension
          document.body.appendChild(link);
          link.click();
          link.remove();
        })
      )
      .catch((errors) => {
        // react
      });
  };

  let coursesTable = (
    <Fragment>
      <Popup
        title='Registrar Curso'
        openPopup={openRegister}
        setOpenPopup={setOpenRegister}
      >
        <RegisterCourses closePopup={setOpenRegister} />
      </Popup>

      <Popup
        title='Modificar Curso'
        openPopup={openModify}
        setOpenPopup={setOpenModify}
      >
        <ModifyCourses rowData={group} closePopup={setOpenModify} />
      </Popup>

      <Popup
        title='Crear copia'
        openPopup={openCopy}
        setOpenPopup={setOpenCopy}
      >
        <CopyCourses rowData={group} closePopup={setOpenCopy} />
      </Popup>

      <MaterialTable
        title='Cursos'
        columns={[
          { title: 'Curso', field: 'course' },
          { title: 'Grupo', field: 'group' },
          { title: 'Departamento', field: 'department' },
          { title: 'Instructor', field: 'instructor' },
          { title: 'Tipo de curso', field: 'courseType' },
          { title: 'Periodo', field: 'period' },
          { title: 'Horario', field: 'schedule' },
          { title: 'Visible', field: 'showCourse' },
        ]}
        data={data}
        actions={[
          {
            icon: 'add_box',
            isFreeAction: true,
            iconProps: { style: { color: '#115293' } },
            tooltip: 'Registrar Curso',
            onClick: () => {
              setOpenRegister((openRegister) => !openRegister);
            },
          },
          {
            icon: 'vertical_align_bottom',
            isFreeAction: true,
            iconProps: { style: { color: '#115293' } },
            tooltip: 'Generar lista de cursos',
            onClick: () => {
              downloadCoursesList();
            },
          },
          {
            icon: 'delete',
            iconProps: { style: { color: '#B70000' } },
            tooltip: 'Eliminar curso',
            onClick: (event, rowData) => {
              deleteUserHandler(rowData.group);
            },
          },
          {
            icon: 'edit',
            iconProps: { style: { color: '#115293' } },
            tooltip: 'Editar/Mas información',
            onClick: (event, rowData) => {
              setGroup(rowData.group);
              setOpenModify((openModify) => !openModify);
            },
          },
          {
            icon: 'content_copy',
            tooltip: 'Copiar/Clonar',
            onClick: (event, rowData) => {
              setGroup(rowData.group);
              setOpenCopy((openCopy) => !openCopy);
            },
          },
        ]}
        options={{
          pageSize: 25,
          pageSizeOptions: [25, 50, 75, 100],
          headerStyle: {
            backgroundColor: '#808080',
            color: '#FFF',
            fontWeight: 'bold',
          },
          filterCellStyle: {
            backgroundColor: '#01579b',
            color: '#FFF',
          },
          searchFieldStyle: {
            backgroundColor: '#E8E8E8',
            color: 'black',
          },
        }}
        localization={{
          header: {
            actions: 'Acciones',
          },
          toolbar: {
            searchTooltip: 'Buscar',
            searchPlaceholder: 'Buscar',
          },
          pagination: {
            labelDisplayedRows: '{from}-{to} de {count}',
            labelRowsSelect: 'Filas',
            firstAriaLabel: 'Primera pagina',
            firstTooltip: 'Primera pagina',
            previousAriaLabel: 'Pagina anterior',
            previousTooltip: 'Pagina anterior',
            nextAriaLabel: 'Siguiente pagina',
            nextTooltip: 'Siguiente pagina',
            lastAriaLabel: 'Ultima pagina',
            lastTooltip: 'Ultima pagina',
          },
          body: {
            emptyDataSourceMessage: 'No hay cursos registrados',
          },
        }}
      />
    </Fragment>
  );

  if (loading) {
    coursesTable = <Spinner />;
  }

  return (
    <div style={styles}>
      {coursesTable}{' '}
      <SnackBar
        openSnackBar={snackStatus.openSnackBar}
        closeSnackBar={() => setSnackStatus({ openSnackBar: false })}
        snackBarMessage={snackStatus.snackBarMessage}
        severity={snackStatus.severity}
      />
    </div>
  );
};

export default CoursesOverview;
