import React, { useState, useLayoutEffect } from 'react';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { useForm, Form } from '../../shared/useForm';
import Controls from '../../components/Controls/Controls';
import AutoComplete from '../../components/Controls/AutoComplete';
import Spinner from '../../components/UI/Spinner/Spinner';
import axios from 'axios';

const getDepartment = () => [
  {
    id: 'ELÉCTRICA, ELECTRÓNICA, BIOMÉDICA',
    title: 'ELÉCTRICA, ELECTRÓNICA, BIOMÉDICA',
  },
  { id: 'SISTEMAS E INFORMÁTICA', title: 'SISTEMAS E INFORMÁTICA' },
  { id: 'METAL MECÁNICA Y AERONÁUTICA', title: 'METAL MECÁNICA Y AERONÁUTICA' },
  { id: 'INDUSTRIAL', title: 'INDUSTRIAL' },
  { id: 'CIENCIAS BÁSICAS', title: 'CIENCIAS BÁSICAS' },
  { id: 'ECONÓMICO ADMINISTRATIVO', title: 'ECONÓMICO ADMINISTRATIVO' },
];

const getCourseType = () => [
  { id: 'PROFESIONAL', title: 'PROFESIONAL' },
  { id: 'DOCENTE', title: 'DOCENTE' },
];

const getTrainingType = () => [
  { id: 'CURSO', title: 'CURSO' },
  { id: 'TALLER', title: 'TALLER' },
  { id: 'CURSO/TALLER', title: 'CURSO/TALLER' },
  { id: 'CONFERENCIA', title: 'CONFERENCIA' },
  { id: 'DIPLOMADO', title: 'DIPLOMADO' },
];

const getAddressedTo = () => [
  {
    id: 'ELÉCTRICA, ELECTRÓNICA, BIOMÉDICA',
    title: 'ELÉCTRICA, ELECTRÓNICA, BIOMÉDICA',
  },
  { id: 'SISTEMAS E INFORMÁTICA', title: 'SISTEMAS E INFORMÁTICA' },
  { id: 'METAL MECÁNICA Y AERONÁUTICA', title: 'METAL MECÁNICA Y AERONÁUTICA' },
  { id: 'INDUSTRIAL', title: 'INDUSTRIAL' },
  { id: 'CIENCIAS BÁSICAS', title: 'CIENCIAS BÁSICAS' },
  { id: 'ECONÓMICO ADMINISTRATIVO', title: 'ECONÓMICO ADMINISTRATIVO' },
  { id: 'INTERESADOS', title: 'INTERESADOS' },
];

const initialValues = {
  course: '',
  group: '',
  instructor: '',
  department: '',
  courseType: '',
  trainingType: '',
  periodStart: new Date(),
  periodEnd: new Date(new Date().setDate(38)),
  hourStart: new Date(new Date().setHours(12, 0)),
  hourEnd: new Date(new Date().setHours(12, 0)),
  hourNumber: '',
  classroom: '',
  partakerLimit: '',
  showCourse: false,
  addressedTo: '',
  observations: '',
};

const ModifyCourses = (props) => {
  const [instructor, setInstructor] = useState('');
  const [loading, setLoading] = useState(false);

  useLayoutEffect(() => {
    request();
    callbackFunction();
    // eslint-disable-next-line
  }, []);

  const request = () => {
    setLoading((loading) => !loading);
    //let url ='/get-course-data'
    axios
      .get('/get-course-data/' + props.rowData)
      .then((response) => {
        setValues({
          course: response.data.course,
          group: response.data.group,
          instructor: response.data.instructor,
          department: response.data.department,
          courseType: response.data.courseType,
          trainingType: response.data.trainingType,
          periodStart: new Date(response.data.periodStart),
          periodEnd: new Date(response.data.periodEnd),
          hourStart: new Date(response.data.hourStart),
          hourEnd: new Date(response.data.hourEnd),
          hourNumber: response.data.hourNumber,
          classroom: response.data.classroom,
          partakerLimit: response.data.partakerLimit,
          showCourse: Boolean(response.data.showCourse),
          addressedTo: response.data.addressedTo,
          observations: response.data.observations,
        });
        setLoading((loading) => !loading);
      })
      .catch((error) => {
        if (error.response) {
          //Se realizo la peticion y el servidor respondio con un codigo de status
          window.alert('Error: ' + error.response.data.message);
        } else if (error.request) {
          //Se realizo la peticion pero no se recibio una respuesta
          window.alert('Error: ' + error.request.data.message);
        } else {
          //Algo ocurrio al preparar la peticion que desencadeno un error
          console.log('Error: ' + error.message);
        }
        console.log(error.config);
      });
  };

  const validate = (fieldValues = values) => {
    let temp = { ...errors };
    if ('course' in fieldValues)
      temp.course = fieldValues.course ? '' : 'Campo requerido';
    if ('group' in fieldValues)
      temp.group = fieldValues.group ? '' : 'Campo requerido';
    if ('partakerLimit' in fieldValues)
      temp.partakerLimit = fieldValues.partakerLimit ? '' : 'Campo requerido';
    if (fieldValues.hourStart > fieldValues.hourEnd) {
      temp.hourStart = 'Hora inicial es mayor a hora final';
      temp.hourEnd = 'Hora inicial es mayor a hora final';
    } else {
      temp.hourStart = '';
      temp.hourEnd = '';
    }
    setErrors({
      ...temp,
    });

    if (fieldValues == values) return Object.values(temp).every((x) => x == '');
  };

  const { values, errors, setErrors, handleInputChange, setValues } = useForm(
    initialValues,
    true,
    validate
  );

  const callbackFunction = (childData) => {
    setInstructor(childData);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validate()) {
      setLoading((loading) => !loading);
      let post = {
        course: values.course,
        group: values.group,
        instructor: instructor,
        department: values.department,
        courseType: values.courseType,
        trainingType: values.trainingType,
        //Se restan 7 horas a periodStart, periodEnd, hourStart y hourEnd debido a que
        //el valor de new Date() esta en UTC+0
        periodStart: new Date(
          values.periodStart.setHours(values.periodStart.getHours() - 7)
        ),
        periodEnd: new Date(
          values.periodEnd.setHours(values.periodEnd.getHours() - 7)
        ),
        hourStart: new Date(
          values.hourStart.setHours(values.hourStart.getHours() - 7)
        ),
        hourEnd: new Date(
          values.hourEnd.setHours(values.hourEnd.getHours() - 7)
        ),

        hourNumber: values.hourNumber,
        classroom: values.classroom,
        partakerLimit: values.partakerLimit,
        showCourse: values.showCourse,
        addressedTo: values.addressedTo,
        observations: values.observations,
      };
      //let url ='/auth/modify-courses'
      axios
        .put('/auth/modify-courses', post)
        .then((response) => {
          props.closePopup(false);
        })
        .catch((error) => {
          setLoading((loading) => !loading);
          if (error.response) {
            //Se realizo la peticion y el servidor respondio con un codigo de status
            window.alert('Error: ' + error.response.data.message);
          } else if (error.request) {
            //Se realizo la peticion pero no se recibio una respuesta
            window.alert('Error: ' + error.request.data.message);
          } else {
            //Algo ocurrio al preparar la peticion que desencadeno un error
            console.log('Error: ' + error.message);
          }
          console.log(error.config);
        });
    }
  };

  let form = (
    <Form onSubmit={handleSubmit}>
      <Grid container>
        <Grid item xs={6}>
          <Controls.Input
            name='course'
            label='Curso'
            placeholder='Curso'
            value={values.course}
            onChange={handleInputChange}
            error={errors.course}
            inputProps={{ maxLength: 200 }}
          />
          <Controls.Input
            disabled={true}
            name='group'
            label='Grupo'
            placeholder='GGG-AAAA-PERIODO'
            value={values.group.replace(/\s/g, '').toUpperCase()}
            onChange={handleInputChange}
            error={errors.group}
            inputProps={{ maxLength: 12 }}
          />
          <AutoComplete
            name='instructor'
            parentCallback={callbackFunction}
            instructorComp={instructor}
            getOptionLabel={values.instructor}
          />

          <Controls.Select
            name='department'
            label='Departamento'
            options={getDepartment()}
            value={values.department}
            onChange={handleInputChange}
            error={errors.department}
          />
          <Controls.Select
            name='courseType'
            label='Tipo de curso'
            options={getCourseType()}
            value={values.courseType}
            onChange={handleInputChange}
            error={errors.courseType}
          />
          <Controls.Select
            name='trainingType'
            label='Tipo de capacitacion'
            options={getTrainingType()}
            value={values.trainingType}
            onChange={handleInputChange}
            error={errors.trainingType}
          />
        </Grid>
        <Grid item xs={6}>
          <Controls.DatePicker
            name='periodStart'
            label='Fecha inicial (d/m/a)'
            value={values.periodStart}
            onChange={handleInputChange}
            maxDate={values.periodEnd}
          />

          <Controls.DatePicker
            name='periodEnd'
            label='Fecha final (d/m/a)'
            value={values.periodEnd}
            onChange={handleInputChange}
          />

          <Controls.TimePicker
            ampm={false}
            name='hourStart'
            label='Hora inicial'
            value={values.hourStart}
            onChange={handleInputChange}
            KeyboardButtonProps={{
              'aria-label': 'change time',
            }}
            error={errors.hourStart}
          />
          <Controls.TimePicker
            ampm={false}
            name='hourEnd'
            label='Hora final'
            value={values.hourEnd}
            onChange={handleInputChange}
            KeyboardButtonProps={{
              'aria-label': 'change time',
            }}
            error={errors.hourEnd}
          />
          <Controls.Input
            name='hourNumber'
            label='Horas del curso'
            placeholder='Horas del curso'
            type='number'
            value={values.hourNumber}
            onChange={handleInputChange}
            error={errors.hourNumber}
            inputProps={{ min: 0 }}
          />
          <Controls.Input
            name='classroom'
            label='Aula'
            placeholder='Aula'
            value={values.classroom.replace(/\s/g, '').toUpperCase()}
            onChange={handleInputChange}
            error={errors.classroom}
            inputProps={{ maxLength: 10 }}
          />
          <Controls.Input
            name='partakerLimit'
            label='Limite de participantes'
            placeholder='Grupo'
            type='number'
            value={values.partakerLimit}
            onChange={handleInputChange}
            error={errors.partakerLimit}
            inputProps={{ min: 0 }}
          />

          <Controls.Select
            name='addressedTo'
            label='Dirigido a:'
            options={getAddressedTo()}
            value={values.addressedTo}
            onChange={handleInputChange}
            error={errors.addressedTo}
          />
          <Controls.Input
            name='observations'
            label='Observaciones'
            placeholder='Grupo'
            value={values.observations}
            onChange={handleInputChange}
            error={errors.observations}
          />

          <Controls.Checkbox
            name='showCourse'
            label='Mostrar Curso'
            value={values.showCourse}
            onChange={handleInputChange}
          />

          <Button
            type='submit'
            fullWidth
            variant='contained'
            color='primary'
            style={{ marginTop: '15px' }}
          >
            ACEPTAR
          </Button>
        </Grid>
      </Grid>
    </Form>
  );

  if (loading) {
    form = <Spinner />;
  }
  return <div>{form}</div>;
};

export default ModifyCourses;
