import React from 'react';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { apiStates, GetApi } from '../../shared/getApi';

//Este componente solo funciona como autocomplete para el instructor
//No es reutilizable con otros datos
const AutoComplete = (props) => {
  // '/courses-overview/ac-instructors'
  const { state, error, data } = GetApi(
    '/courses-overview/ac-instructors'
  );

  const usersList = Object.keys(data).map((key) => data[key].instructor);
  console.log('AUTOCOMPLETE');
  const onTagChange = (event, value) => {
    props.parentCallback(value);
  };

  switch (state) {
    case apiStates.ERROR:
      return <p>ERROR: {error || 'General error'}</p>;
    case apiStates.SUCCESS:
      return (
        <Autocomplete
          id='instructor'
          style={{ width: 300 }}
          getOptionSelected={(option, value) => option === value}
          getOptionLabel={(option) => option}
          options={usersList}
          onChange={onTagChange}
          renderInput={(params) => <TextField {...params} label='Instructor' />}
        />
      );
    default:
      return <p>Cargando..</p>;
  }
};

//Verificar que component se renderice solo cuando instructorComp (props) cambie
const areEqual = (prevProps, nextProps) => {
  return prevProps.instructorComp === nextProps.instructorComp;
};

export default React.memo(AutoComplete, areEqual);
